import { POPOVER_ITEM_TYPE } from '../../constants';

import parseNavItem from '../../utils/parseNavItem';

import SearchTerm from './SearchTerm';
import SectionHeader from './SectionHeader';
import UserHistory from './UserHistory';
import InstanceHistory from './InstanceHistory';
import Navigation from './Navigation';

const getPopoverItem = ({ item, dispatch, isActive, term, index }) => {
    const itemText = item.columns[0].displayValue;

    switch (item.type) {
        case POPOVER_ITEM_TYPE.USER_SEARCH_TERM:
            return (
                <SearchTerm
                    isActive={isActive}
                    index={index}
                    name={itemText}
                    dispatch={dispatch}
                    term={term}
                />
            );

        case POPOVER_ITEM_TYPE.SECTION_HEADER:
            return <SectionHeader label={itemText} dispatch={dispatch} />;

        case POPOVER_ITEM_TYPE.POPULAR_QUERY:
            return (
                <InstanceHistory
                    isActive={isActive}
                    index={index}
                    name={itemText}
                    dispatch={dispatch}
                    term={term}
                />
            );
        case POPOVER_ITEM_TYPE.USER_HISTORY:
            return (
                <UserHistory
                    isActive={isActive}
                    index={index}
                    name={itemText}
                    dispatch={dispatch}
                    term={term}
                    showActions={!term}
                />
            );
        case POPOVER_ITEM_TYPE.PERSONAL_CLICK:
            return (
                <Navigation
                    isActive={isActive}
                    index={index}
                    name={itemText}
                    dispatch={dispatch}
                    icon={
                        parseNavItem(item).table === 'sc_cat_item'
                            ? 'clipboard-outline'
                            : 'document-outline'
                    }
                    term={term}
                />
            );
        default:
            return '';
    }
};

export default getPopoverItem;
