import { actionTypes } from '@servicenow/ui-core';

import {
    NOW_BUTTON_BARE_CLICKED,
    NOW_BUTTON_ICONIC_CLICKED,
    INPUT_FOCUSED_IN,
    INPUT_FOCUSED_OUT,
    ITEM_SELECTED,
    CLEAR_SEARCH_SELECTED,
    CLEAR_SEARCH_MUTATION,
    CLEAR_SEARCH_MUTATION_SUCCEEDED,
    CLEAR_SEARCH_MUTATION_FAILED,
    ARROW_DOWN_PRESSED,
    ARROW_UP_PRESSED,
    ARROW_LEFT_PRESSED,
    ARROW_RIGHT_PRESSED,
    INPUT_CHANGED,
    HIDE_POPOVER,
    SHOW_POPOVER,
    MOUSE_OVERED,
    SUGGESTION_FETCH,
    SUGGESTION_FETCH_STARTED,
    SUGGESTION_FETCH_SUCCEEDED,
    SUGGESTION_FETCH_FAILED,
    FIT_RESIZE,
} from '../actions';

import {
    nowButtonClickedEffect,
    clearSearchSelectedEffect,
    inputFocusedInEffect,
    inputFocusedOutEffect,
    itemSelectedEffect,
    arrowUpEffect,
    arrowDownEffect,
    arrowLeftEffect,
    arrowRightEffect,
    mouseOverEffect,
    showPopoverEffect,
    hidePopoverEffect,
    inputChangeEffect,
} from '../effects/inputEffects';

import {
    suggestionFetchEffect,
    suggestionFetchStartedEffect,
    suggestionFetchSucceededEffect,
    suggestionFetchFailedEffect,
} from '../effects/suggestionEffects';

import {
    clearSearchEffect,
    clearSearchSucceededEffect,
    clearSearchFailedEffect,
} from '../effects/clearSearchEffects';

import fitResizeEffect from '../effects/fitEffects';

import createAfterInterceptor from '../utils/interceptor';
import effectProxy from '../utils/effectProxy';

const {
    COMPONENT_CONNECTED,
    COMPONENT_PROPERTY_CHANGED,
    COMPONENT_DISCONNECTED,
    COMPONENT_RENDERED,
} = actionTypes;

const setTermToState = ({ properties, updateState }) => {
    const { searchTerm } = properties;
    updateState({
        path: 'term',
        value: searchTerm,
        operation: 'set',
    });
};

export default {
    [COMPONENT_CONNECTED]({ dispatch, properties, updateState }) {
        setTermToState({ dispatch, properties, updateState });

        window.addEventListener('resize', () => {
            dispatch(FIT_RESIZE);
        });
    },
    [COMPONENT_DISCONNECTED]() {
        window.removeEventListener('resize', () => {});
    },
    [COMPONENT_RENDERED]({ dispatch }) {
        dispatch(FIT_RESIZE);
    },
    [COMPONENT_PROPERTY_CHANGED]({ properties, action, updateState }) {
        const {
            payload: { name },
        } = action;
        if (name === 'searchTerm') {
            setTermToState({ properties, updateState });
        }
    },
    [SUGGESTION_FETCH]: effectProxy(suggestionFetchEffect),
    [SUGGESTION_FETCH_STARTED]: {
        effect: suggestionFetchStartedEffect,
        stopPropagation: true,
    },
    [SUGGESTION_FETCH_SUCCEEDED]: {
        effect: suggestionFetchSucceededEffect,
        stopPropagation: true,
    },
    [SUGGESTION_FETCH_FAILED]: {
        effect: suggestionFetchFailedEffect,
        stopPropagation: true,
    },
    [INPUT_CHANGED]: {
        effect: inputChangeEffect,
        interceptors: createAfterInterceptor(SHOW_POPOVER),
        stopPropagation: true,
    },
    [INPUT_FOCUSED_IN]: {
        effect: inputFocusedInEffect,
        interceptors: createAfterInterceptor(SHOW_POPOVER),
        stopPropagation: true,
    },
    [INPUT_FOCUSED_OUT]: {
        effect: inputFocusedOutEffect,
        stopPropagation: true,
    },
    [ARROW_UP_PRESSED]: {
        effect: arrowUpEffect,
        interceptors: createAfterInterceptor(SHOW_POPOVER),
        stopPropagation: true,
    },
    [ARROW_DOWN_PRESSED]: {
        effect: arrowDownEffect,
        interceptors: createAfterInterceptor(SHOW_POPOVER),
        stopPropagation: true,
    },
    [ARROW_LEFT_PRESSED]: {
        effect: arrowLeftEffect,
        stopPropagation: true,
    },
    [ARROW_RIGHT_PRESSED]: {
        effect: arrowRightEffect,
        stopPropagation: true,
    },
    [NOW_BUTTON_BARE_CLICKED]: {
        effect: nowButtonClickedEffect,
        stopPropagation: true,
    },
    [NOW_BUTTON_ICONIC_CLICKED]: {
        effect: nowButtonClickedEffect,
        stopPropagation: true,
    },
    [ITEM_SELECTED]: {
        effect: itemSelectedEffect,
        interceptors: createAfterInterceptor(HIDE_POPOVER),
        stopPropagation: true,
    },
    [CLEAR_SEARCH_SELECTED]: {
        effect: clearSearchSelectedEffect,
        stopPropagation: true,
    },
    [CLEAR_SEARCH_MUTATION]: {
        ...clearSearchEffect,
        stopPropagation: true,
    },
    [CLEAR_SEARCH_MUTATION_SUCCEEDED]: {
        effect: clearSearchSucceededEffect,
        stopPropagation: true,
    },
    [CLEAR_SEARCH_MUTATION_FAILED]: {
        effect: clearSearchFailedEffect,
        stopPropagation: true,
    },
    [MOUSE_OVERED]: {
        effect: mouseOverEffect,
        stopPropagation: true,
    },
    [SHOW_POPOVER]: {
        effect: showPopoverEffect,
        stopPropagation: true,
    },
    [HIDE_POPOVER]: {
        effect: hidePopoverEffect,
        stopPropagation: true,
    },
    [FIT_RESIZE]: {
        effect: fitResizeEffect,
        stopPropagation: true,
    },
};
