import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import get from 'lodash/get';
import { CLEAR_SEARCH_FROM_HISTORY_MUTATION_QUERY } from '../constants';

import {
    CLEAR_SEARCH_MUTATION_SUCCEEDED,
    CLEAR_SEARCH_MUTATION_FAILED,
    SUGGESTION_FETCH,
} from '../actions';

const mutationPath =
    'payload.data.GlideSearch_Mutation.clearSearchFromHistory.term';

export const clearSearchEffect = createGraphQLEffect(
    CLEAR_SEARCH_FROM_HISTORY_MUTATION_QUERY,
    {
        variableList: ['searchContextConfigId', 'term'],
        successActionType: CLEAR_SEARCH_MUTATION_SUCCEEDED,
        errorActionType: CLEAR_SEARCH_MUTATION_FAILED,
    }
);

export const clearSearchSucceededEffect = ({
    dispatch,
    properties,
    state,
    action,
}) => {
    const { term } = state;
    const { searchContextConfigId } = properties;
    const deletedQuery = get(action, mutationPath, null);

    // null if soft delete was not successful
    if (!deletedQuery) return;

    dispatch(SUGGESTION_FETCH, { searchContextConfigId, term });
};

export const clearSearchFailedEffect = ({ action, state, updateState }) => {
    updateState({
        ...state,
        fetching: false,
        error: action.payload,
    });
};
