import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import view from './view';
import { SIZE } from './common/constants';
import {
    SEARCH_EXECUTED,
    NAVIGATION_EXECUTED,
    SEARCH_EXECUTED_INTERNAL,
    NAVIGATION_EXECUTED_INTERNAL,
} from './common/actions';

const searchExecutedEffect = ({ action: { payload }, dispatch }) => {
    dispatch(SEARCH_EXECUTED, payload);
};

const navigationExecutedEffect = ({ action: { payload }, dispatch }) => {
    dispatch(NAVIGATION_EXECUTED, payload);
};

/**
 * Search combobox allows user to search and provides search autocomplete capability while user is typing.
 *
 * ```jsx
 * <sn-search-combobox
 *      search-context-config-id="0ce0997f7320001052c7d5fdbdf6a746"
 *      placement="body"
 *      size="lg"
 * />
 * ```
 *
 * @seismicElement sn-search-combobox
 * @summary Seismic component to search and get autocomplete suggestions while typing.
 */
createCustomElement('sn-search-combobox', {
    renderer: { type: snabbdom },
    view,
    properties: {
        /**
         * Placeholder tells search combobox what to use for the placeholder in the input box before it has a value.
         * @type {string}
         */
        placeholder: {
            default: t('Search'),
            schema: {
                type: 'string',
            },
        },
        /**
         * Initial search term for the search input.
         * @type {string}
         */
        searchTerm: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * sys_id of the search context configuration.
         * Given searchContextConfigId, our search service can retrieve all the search and autocomplete configuration metadata.
         * This sys_id is defined in sys_search_context_config table.
         * @type {string}
         */
        searchContextConfigId: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Placement tells search combobox where it is rendered. Two values are supported: header and body.
         * It impacts the component's visual style.
         * @type {('header'|'body')}
         */
        placement: {
            default: 'body',
            schema: {
                type: 'string',
            },
        },
        /**
         * This property enables/disables autocomplete capability.
         * @type {boolean}
         */
        disableAutocomplete: {
            default: false,
            schema: {
                type: 'boolean',
            },
        },
        /**
         * Specifies if the component is being rendered on mobile or desktop.
         * There are two accepted values for this property, 'sm' for mobile version and 'lg' for desktop version.
         * @type {('sm'|'lg')}
         */
        size: {
            default: SIZE.LARGE,
            schema: {
                type: 'string',
                enum: ['sm', 'lg'],
            },
        },
    },
    actionHandlers: {
        [SEARCH_EXECUTED_INTERNAL]: {
            effect: searchExecutedEffect,
            stopPropagation: true,
        },
        [NAVIGATION_EXECUTED_INTERNAL]: {
            effect: navigationExecutedEffect,
            stopPropagation: true,
        },
    },
    dispatches: {
        /**
         * Dispatched when search is fired. Payload contains search term and search context config id.
         * @type {{searchTerm: string, searchContextConfigId: string}}
         */
        'SN_SEARCH_COMBOBOX#SEARCH_EXECUTED': {
            schema: {
                type: 'object',
                properties: {
                    searchTerm: { type: 'string' },
                    searchContextConfigId: { type: 'string' },
                },
            },
        },
        /**
         * Dispatched when user clicks on a navigable item.
         * Instead of routing to a search page, this action will route to a content page.
         * @type {{table: string, sysId: string, url: string}}
         */
        'SN_SEARCH_COMBOBOX#NAVIGATION_EXECUTED': {
            schema: {
                type: 'object',
                properties: {
                    table: { type: 'string' },
                    sysId: { type: 'string' },
                    url: { type: 'string' },
                },
            },
        },
    },
});
