const SUGGESTION_QUERY = `query($searchContextConfigId: String!, $term: String!){
    GlideSearch_Query {
        suggestions(searchContextConfigId: $searchContextConfigId, searchTerm: $term) {
            term
            data {
                name
                records {
                    type
                    columns {
                        label
                        fieldName
                        value
                        displayValue
                    }
                }
            }
        }
    }
}`;

const CLEAR_SEARCH_FROM_HISTORY_MUTATION_QUERY = `mutation($searchContextConfigId: String!, $term: String!){
    GlideSearch_Mutation {
        clearSearchFromHistory(searchContextConfigId: $searchContextConfigId, searchTerm: $term) {
            term
        }
    }
}`;

