import '@servicenow/now-icon';
import isEmpty from 'lodash/isEmpty';
import getPopoverItem from './PopoverItems';

const view = ({ activeRowIndex, data, term, dispatch, nowId }) => {
    const isTyping = term.length > 0;

    if (!isTyping && (!data || data.length === 0)) {
        return null;
    }

    const items = data;
    const ariaPopupType = isEmpty(term) ? 'grid' : 'listbox';

    return (
        // eslint-disable-next-line jsx-a11y/no-noninteractive-element-to-interactive-role
        <ul
            className="search-popover"
            id={`${nowId}_owned_grid`}
            role={ariaPopupType}
        >
            {items.map((item, index) => {
                const isActive = index === activeRowIndex;
                return getPopoverItem({
                    item,
                    dispatch,
                    isActive,
                    term,
                    index,
                });
            })}
        </ul>
    );
};

export default view;
