import { POPOVER_ITEM_TYPE } from '../constants';

function transformSuggestionResponse(dataResponse) {
    const output = [];
    dataResponse.forEach(group => {
        if (group.name) {
            output.push({
                type: POPOVER_ITEM_TYPE.SECTION_HEADER,
                columns: [
                    {
                        fieldName: 'name',
                        label: 'Name',
                        value: group.name,
                        displayValue: group.name,
                    },
                ],
            });
        }
        output.push(...group.records);
    });
    return output;
}

export default transformSuggestionResponse;
