import Item from './PopoverItem';
import highlightMatch from '../../utils/highlight';
import { CLEAR_SEARCH_SELECTED } from '../../actions';

const view = ({ term, name, index, dispatch, isActive, showActions }) => {
    const icon = 'magnifying-glass-fill';
    const actions = [
        {
            clickActionType: CLEAR_SEARCH_SELECTED,
            icon: 'circle-close-outline',
        },
    ];
    const primaryText = highlightMatch(term, name);
    return (
        <Item
            isActive={isActive}
            index={index}
            icon={icon}
            primaryText={primaryText}
            suggestionLabel={name}
            itemActions={actions}
            dispatch={dispatch}
            showActions={showActions}
        />
    );
};

export default view;
