import '@servicenow/now-button';

import { SHOW_SEARCH_PANEL } from '../actions';
import { KEYCODE } from '../../common/constants';

const view = ({ dispatch }) => (
    <div className="mobile-search-init mobile-search-init--header-view">
        <now-button-iconic
            bare
            type="button"
            icon="magnifying-glass-fill"
            size="md"
            variant="tertiary"
            on-click={() => dispatch(SHOW_SEARCH_PANEL)}
            on-keydown={evt => {
                if (evt.code === KEYCODE.ENTER || evt.code === KEYCODE.SPACE) {
                    dispatch(SHOW_SEARCH_PANEL);
                }
            }}
        />
    </div>
);

export default view;
