import isEmpty from 'lodash/isEmpty';
import {
    SUGGESTION_FETCH,
    SEARCH_EXECUTED_INTERNAL,
    NAVIGATION_EXECUTED_INTERNAL,
    CLEAR_SEARCH_MUTATION,
    ITEM_SELECTED,
} from '../actions';

import {
    POPOVER_ITEM_TYPE,
    EVENT_TYPE,
    AUTOCOMPLETE_QUERY_EVENT,
    INFO_EVENT,
    KEYBOARD_NAVIGATION,
} from '../constants';

import signalTracker from '../utils/signalTracker';
import parseNavItem from '../utils/parseNavItem';
import getRowColIndex from '../utils/keyboardNavigation';

const _getRelativeClickIndex = (data, activeRowIndex, itemType) => {
    const items = data.slice(0, activeRowIndex + 1);
    const itemClickIndex = items.filter(item => item.type === itemType).length;

    return itemClickIndex;
};

const _getValue = (data, index) => {
    const item = data[index];

    if (!item) return '';

    return item.columns[0].value;
};

const _setInputFocus = host => {
    const input = host.shadowRoot.querySelector('input');
    if (input) input.focus();
};

/**
 * Multiple NDS components may exist within `sn-search-combobox` and
 * all `now-button-bare` dispatch the same action. Instead of having
 * a huge effect, we will just re-dispatch the appropriate action
 * by leveraging the property `append-to-payload`
 *
 * Ex:
 *  <now-button-bare
 *     append-to-payload={{
 *        action: SHOW_MODAL, <-- Action to redispatch
 *        payload: {          <-- Payload to pass to defined action
 *           type, index
 *         }
 *     }}
 *     ... />
 *
 */
export const nowButtonClickedEffect = ({
    action: {
        payload: { action, payload },
    },
    dispatch,
}) => {
    dispatch(action, payload);
};

export const clearSearchSelectedEffect = ({
    host,
    dispatch,
    state,
    properties,
}) => {
    const { activeRowIndex, data } = state;
    const { searchContextConfigId } = properties;
    const term = _getValue(data, activeRowIndex);

    dispatch(CLEAR_SEARCH_MUTATION, { searchContextConfigId, term });
    _setInputFocus(host);
};

export const inputFocusedInEffect = ({
    state,
    properties,
    updateState,
    dispatch,
}) => {
    const { term, suggestionSearchTermQuery } = state;
    const { searchContextConfigId, disableAutocomplete } = properties;

    updateState({
        ...state,
        inputHasFocus: true,
    });

    if (term !== suggestionSearchTermQuery) {
        if (!disableAutocomplete) {
            dispatch(SUGGESTION_FETCH, {
                searchContextConfigId,
                term,
            });
        }
    }
};

export const inputFocusedOutEffect = ({ state, updateState }) => {
    updateState({
        ...state,
        inputHasFocus: false,
    });
};

const _navigationItemClicked = type => {
    const navTypes = [
        POPOVER_ITEM_TYPE.EXACT_MATCH,
        POPOVER_ITEM_TYPE.NAVIGATION,
        POPOVER_ITEM_TYPE.PERSONAL_CLICK,
    ];
    return navTypes.includes(type);
};

const _enterKeyPressedInInput = activeRowIndex => activeRowIndex === -1;

const handlers = {
    [EVENT_TYPE.SEARCH_FROM_INPUT](
        term,
        searchContextConfigId,
        updateState,
        dispatch
    ) {
        updateState({
            path: 'term',
            value: term,
            operation: 'set',
        });
        dispatch(SEARCH_EXECUTED_INTERNAL, {
            searchContextConfigId,
            searchTerm: term,
        });
    },
    [EVENT_TYPE.SEARCH_FROM_POPOVER](
        term,
        searchContextConfigId,
        disableAutocomplete,
        updateState,
        dispatch
    ) {
        updateState({
            path: 'term',
            value: term,
            operation: 'set',
        });
        dispatch(SEARCH_EXECUTED_INTERNAL, {
            searchContextConfigId,
            searchTerm: term,
        });

        if (!disableAutocomplete) {
            dispatch(SUGGESTION_FETCH, { searchContextConfigId, term });
        }
    },
    [EVENT_TYPE.NAVIGATION](
        searchContextConfigId,
        term,
        data,
        activeRowIndex,
        item,
        dispatch
    ) {
        const navData = parseNavItem(item);
        signalTracker.trackEvent(AUTOCOMPLETE_QUERY_EVENT, INFO_EVENT, {
            searchContextConfigId,
            queryTerm: term,
            selectedQuery: navData.label,
            queryType: item.type,
            clickIndex: _getRelativeClickIndex(data, activeRowIndex, item.type),
        });

        dispatch(NAVIGATION_EXECUTED_INTERNAL, navData.actionPayload);
    },
};

export const itemSelectedEffect = ({
    state,
    properties,
    dispatch,
    updateState,
}) => {
    const { term, activeRowIndex, data } = state;
    const { searchContextConfigId, disableAutocomplete } = properties;

    const isEnterKeyPressedInInput = _enterKeyPressedInInput(activeRowIndex);
    if (isEnterKeyPressedInInput) {
        handlers[EVENT_TYPE.SEARCH_FROM_INPUT](
            term,
            searchContextConfigId,
            updateState,
            dispatch
        );
        return;
    }

    const item = data[activeRowIndex];
    const navigating = _navigationItemClicked(item.type);

    if (navigating) {
        handlers[EVENT_TYPE.NAVIGATION](
            searchContextConfigId,
            term,
            data,
            activeRowIndex,
            item,
            dispatch
        );
    } else {
        handlers[EVENT_TYPE.SEARCH_FROM_POPOVER](
            _getValue(data, activeRowIndex),
            searchContextConfigId,
            disableAutocomplete,
            updateState,
            dispatch
        );

        signalTracker.trackEvent(AUTOCOMPLETE_QUERY_EVENT, INFO_EVENT, {
            searchContextConfigId,
            queryTerm: term,
            selectedQuery: _getValue(data, activeRowIndex),
            queryType: item.type,
            clickIndex: isEmpty(term)
                ? _getRelativeClickIndex(data, activeRowIndex, item.type)
                : activeRowIndex + 1, // To have the index start from 1
        });
    }
};

const _setPopoverSuggestionFocus = (host, row, column) => {
    const suggestions = host.shadowRoot.querySelectorAll('ul > li');
    const focusedOption = !isEmpty(suggestions) ? suggestions[row] : null;

    if (focusedOption && column > 0) {
        const selectorQuery =
            column === 1
                ? 'div.search-popover__item-metadata'
                : 'now-button-iconic.search-popover__item-button';
        const focusedColumn = focusedOption.querySelector(selectorQuery);

        if (focusedColumn) focusedColumn.focus();
    }
};

export const arrowDownEffect = ({ action, state, updateState }) => {
    const { data, activeRowIndex, activeColumnIndex } = state;
    const {
        payload: { host },
    } = action;
    const { rowIndex, columnIndex } = getRowColIndex(
        activeRowIndex,
        activeColumnIndex,
        data,
        KEYBOARD_NAVIGATION.DOWN
    );

    _setPopoverSuggestionFocus(host, rowIndex, columnIndex);
    updateState({
        ...state,
        activeRowIndex: rowIndex,
        activeColumnIndex: columnIndex,
    });
};

export const arrowUpEffect = ({ action, state, updateState }) => {
    const { data, activeRowIndex, activeColumnIndex } = state;
    const {
        payload: { host },
    } = action;
    const { rowIndex, columnIndex } = getRowColIndex(
        activeRowIndex,
        activeColumnIndex,
        data,
        KEYBOARD_NAVIGATION.UP
    );

    if (rowIndex === -1) _setInputFocus(host);
    else _setPopoverSuggestionFocus(host, rowIndex, columnIndex);
    updateState({
        ...state,
        activeRowIndex: rowIndex,
        activeColumnIndex: columnIndex,
    });
};

