export const mouseOverEffect = ({ action, state, updateState }) => {
    updateState({
        ...state,
        activeRowIndex: action.payload.index,
        activeColumnIndex: 0,
    });
};

export const showPopoverEffect = ({ state, updateState }) => {
    updateState({
        ...state,
        showPopover: true,
    });
};

export const hidePopoverEffect = ({ state, updateState }) => {
    updateState({
        ...state,
        showPopover: false,
        activeRowIndex: -1,
        activeColumnIndex: 0,
    });
};

export const inputChangeEffect = ({
    host,
    properties,
    action,
    dispatch,
    updateState,
}) => {
    const { term } = action.payload;
    const { searchContextConfigId, disableAutocomplete } = properties;

    // when search term is cleared, set focus to input
    if (term === '') _setInputFocus(host);

    updateState({
        path: 'term',
        value: term,
        operation: 'set',
    });

    if (!disableAutocomplete) {
        dispatch(SUGGESTION_FETCH, { searchContextConfigId, term });
    }
};

export const _isPopoverButtonEvent = event => {
    const path = event.composedPath ? event.composedPath() : event.path;
    const isClearHistoryButton = path.find(
        element => element.className === 'search-popover__item-button'
    );
    return isClearHistoryButton;
};

export const _isPopoverTermEvent = event => {
    const path = event.composedPath ? event.composedPath() : event.path;
    const sourceElement = path.length > 0 ? path[0] : null;
    return (
        sourceElement &&
        sourceElement.tagName === 'DIV' &&
        sourceElement.className === 'search-popover__item-metadata'
    );
};

export const enterKeyHandler = (host, event, dispatch) => {
    const path = event.composedPath ? event.composedPath() : event.path;
    const sourceElement = path.length > 0 ? path[0] : null;

    const isInputEvent = sourceElement && sourceElement.tagName === 'INPUT';
    const isPopoverTermEvent = _isPopoverTermEvent(event);

    if (isInputEvent || isPopoverTermEvent) {
        dispatch(ITEM_SELECTED);
    }
};

export const tabKeyHandler = (host, event) => {
    const isPopoverTermEvent = _isPopoverTermEvent(event);
    const isPopoverButtonEvent = _isPopoverButtonEvent(event);

    if (isPopoverTermEvent || isPopoverButtonEvent) {
        event.preventDefault();
        const clearButton = host.shadowRoot.querySelector(
            'now-button-iconic.input-container__icon--cancel'
        );
        const searchButton = host.shadowRoot.querySelector(
            'now-button-iconic.input-container__icon--search'
        );

        if (clearButton) clearButton.focus();
        else if (searchButton) searchButton.focus();
    }
};
