import '@servicenow/now-button';

import { SHOW_SEARCH_PANEL } from '../actions';
import { KEYCODE } from '../../common/constants';

const view = ({ placeholder, dispatch }) => {
    const _keydownHandler = event => {
        if (event.code === KEYCODE.ENTER || event.code === KEYCODE.SPACE) {
            dispatch(SHOW_SEARCH_PANEL);
        }
    };

    return (
        <div className="mobile-search-init mobile-search-init--page-view">
            <input
                value=""
                on-click={() => dispatch(SHOW_SEARCH_PANEL)}
                placeholder={placeholder}
                on-focus={() => dispatch(SHOW_SEARCH_PANEL)}
                type="text"
                aria-autocomplete="list"
                aria-controls="owned_grid"
                className=""
            />
            <now-button-iconic
                bare
                type="button"
                icon="magnifying-glass-outline"
                size="md"
                variant="tertiary"
                on-keydown={_keydownHandler}
                on-click={() => dispatch(SHOW_SEARCH_PANEL)}
            />
        </div>
    );
};

export default view;
