import { Fragment } from '@servicenow/ui-renderer-snabbdom';

const replaceWhiteSpace = str => (str || '').replace(/\s/g, '\u00a0');

// bold the matched string
const highlightMatch = (match, str) => {
    const matchText = match || '';
    const sourceText = str || '';

    const matchStartIndex = sourceText
        .toLowerCase()
        .indexOf(matchText.toLowerCase());
    const matchEndIndex =
        matchStartIndex !== -1 ? matchStartIndex + matchText.length : 0;

    const regularLeftText = replaceWhiteSpace(
        sourceText.substring(0, matchStartIndex)
    );
    const bold = replaceWhiteSpace(
        sourceText.substring(matchStartIndex, matchEndIndex)
    );
    const regularRightText = replaceWhiteSpace(
        sourceText.substring(matchEndIndex)
    );

    const leftTextVNode = regularLeftText || null;
    const middleTextVNode = bold ? <strong>{bold}</strong> : null;
    const rightTextVNode = regularRightText || null;

    return (
        <Fragment>
            {leftTextVNode}
            {middleTextVNode}
            {rightTextVNode}
        </Fragment>
    );
};

export default highlightMatch;
