import '@servicenow/now-button';
import isEmpty from 'lodash/isEmpty';
import Input from '../../common/view/Input';
import Popover from '../../common/view/Popover';

const view = (state, { dispatch }) => {
    const {
        showPopover,
        term,
        activeRowIndex,
        data,
        inputHasFocus,
        properties: { placeholder, placement, nowId },
    } = state;

    const hasSuggestions = data && data.length > 0;

    const enablePopover = showPopover && hasSuggestions;
    const ariaPopupType = isEmpty(term) ? 'grid' : 'listbox';
    return (
        <div role="search">
            <div
                className={`search-combobox search-combobox--${placement} search-combobox--desktop`}
                role="combobox"
                aria-controls={
                    enablePopover ? `${nowId}_owned_grid` : undefined
                }
                aria-haspopup={ariaPopupType}
                aria-autocomplete="list"
                aria-expanded={enablePopover ? 'true' : 'false'}
            >
                <Input
                    hasFocus={inputHasFocus}
                    showPopover={enablePopover}
                    placeholder={placeholder}
                    term={term}
                    hasSuggestions={hasSuggestions}
                    placement={placement}
                    dispatch={dispatch}
                    nowId={nowId}
                />
                {enablePopover ? (
                    <Popover
                        activeRowIndex={activeRowIndex}
                        data={data}
                        term={term}
                        dispatch={dispatch}
                        nowId={nowId}
                    />
                ) : null}
            </div>
        </div>
    );
};

export default view;
