import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import get from 'lodash/get';
import { SUGGESTION_QUERY } from '../constants';
import {
    SUGGESTION_FETCH_STARTED,
    SUGGESTION_FETCH_SUCCEEDED,
    SUGGESTION_FETCH_FAILED,
} from '../actions';

import transformSuggestionResponse from '../utils/transformSuggestionResponse';

// Suggestion effect needs to be stripped out in the future
export const suggestionFetchEffect = createGraphQLEffect(SUGGESTION_QUERY, {
    variableList: ['searchContextConfigId', 'term'],
    startActionType: SUGGESTION_FETCH_STARTED,
    successActionType: SUGGESTION_FETCH_SUCCEEDED,
    errorActionType: SUGGESTION_FETCH_FAILED,
});

export const suggestionFetchStartedEffect = ({ state, updateState }) => {
    updateState({
        ...state,
        fetching: true,
        activeRowIndex: -1,
        activeColumnIndex: 0,
    });
};

export const suggestionFetchSucceededEffect = ({
    action,
    state,
    updateState,
}) => {
    const data = get(
        action,
        'payload.data.GlideSearch_Query.suggestions.data',
        []
    );

    const suggestionSearchTermQuery = get(
        action,
        'payload.data.GlideSearch_Query.suggestions.term',
        ''
    );

    updateState({
        ...state,
        fetching: false,
        data: transformSuggestionResponse(data),
        suggestionSearchTermQuery,
    });
};

export const suggestionFetchFailedEffect = ({ action, state, updateState }) => {
    updateState({
        ...state,
        fetching: false,
        error: action.payload,
    });
};
