import { PERSONAL_CLICK_FIELDS } from '../constants';

function getColumnByFieldName(columns, fieldName) {
    if (!columns || !Array.isArray(columns)) return {};
    const matches = columns.filter(col => col.fieldName === fieldName);
    return matches.length > 0 ? matches[0] : {};
}

function parseNavItem(navItem) {
    const table =
        navItem.table ||
        getColumnByFieldName(navItem.columns, PERSONAL_CLICK_FIELDS.TABLE)
            .value ||
        '';

    const label =
        navItem.label ||
        getColumnByFieldName(navItem.columns, PERSONAL_CLICK_FIELDS.LABEL)
            .value ||
        '';

    const actionDataCol = getColumnByFieldName(
        navItem.columns,
        PERSONAL_CLICK_FIELDS.ACTION_DATA
    );

    let actionData = {};
    try {
        actionData = JSON.parse(actionDataCol.value);
    } catch (e) {
        console.log(
            `invalid JSON syntax in actionData: ${actionDataCol.value}`
        );
    }

    return {
        table,
        label,
        actionPayload: actionData,
    };
}

export default parseNavItem;
