import { INPUT_CHANGED, HIDE_SEARCH_PANEL, HIDE_POPOVER } from '../actions';

export const showSearchPanelInMobileViewEffect = ({ state, updateState }) => {
    updateState({
        ...state,
        searchPanelOpened: true,
    });
};

export const hideSearchPanelInMobileViewEffect = ({
    state,
    dispatch,
    updateState,
}) => {
    updateState({
        ...state,
        searchPanelOpened: false,
        activeRowIndex: -1,
        term: '',
    });
    dispatch(INPUT_CHANGED, { term: '' });
};

export const handleMobileCancel = ({ dispatch }) => {
    dispatch(HIDE_SEARCH_PANEL);
    dispatch(HIDE_POPOVER);
};
