import '@servicenow/now-icon';
import '@servicenow/now-tooltip';
import {
    INPUT_CHANGED,
    SHOW_POPOVER,
    INPUT_FOCUSED_IN,
    INPUT_FOCUSED_OUT,
    ITEM_SELECTED,
    HIDE_POPOVER,
} from '../actions';
import { PLACEMENT, LABELS } from '../constants';

let shouldFocusInput = false;

const setFocus = isMobileView => vnode => {
    if (isMobileView && vnode.elm) {
        vnode.elm.focus();
    }
};

const removeFocus = isMobileView => vnode => {
    if (isMobileView && vnode.elm) {
        vnode.elm.blur();
    }
};

const view = ({
    hasFocus,
    showPopover,
    placeholder,
    term,
    dispatch,
    isMobileView,
    placement,
    hasSuggestions,
    nowId,
}) => {
    let inputContainerClassName = 'input-container';

    if (showPopover) {
        inputContainerClassName += ' input-container--is-popover-open';
    } else if (hasFocus && placement === 'body') {
        inputContainerClassName += ' input-container--has-focus';
    }
    inputContainerClassName += `${term ? ' input-container--is-typing' : ''}`;
    inputContainerClassName += `${
        hasFocus ? ' input-container--is-focused' : ''
    }`;

    const ariaControls = showPopover
        ? { 'aria-controls': `${nowId}_owned_grid` }
        : {};

    const inputElement = (
        <input
            className="input-container__input"
            aria-autocomplete="list"
            {...ariaControls}
            type="text"
            aria-label={placeholder}
            placeholder={placeholder}
            on-input={({ target: { value } }) => {
                dispatch(INPUT_CHANGED, { term: value });
            }}
            value={term}
            on-focus={() => {
                if (shouldFocusInput) {
                    shouldFocusInput = false;
                }
                dispatch(INPUT_FOCUSED_IN);
            }}
            on-blur={ev => {
                if (shouldFocusInput) {
                    ev.target.focus();
                } else {
                    dispatch(INPUT_FOCUSED_OUT);
                }
            }}
            hook={{
                insert: setFocus(isMobileView),
                destroy: removeFocus(isMobileView),
            }}
        />
    );

    // focus is in input with text or
    // focus is on cancel button
    const showCancel = (term.length && hasFocus) || (term && !hasFocus);

    // Buttons should be large for the desktop in-page view, else medium
    const buttonSize =
        placement === PLACEMENT.BODY && !isMobileView ? 'lg' : 'md';

    const cancelIconName =
        placement === PLACEMENT.BODY && !isMobileView
            ? 'close-outline'
            : 'circle-close-outline';

    const searchIconName =
        placement === PLACEMENT.BODY
            ? 'magnifying-glass-outline'
            : 'magnifying-glass-fill';

    return (
        <div className={inputContainerClassName}>
            {inputElement}
            {showCancel ? (
                <now-button-iconic
                    append-to-payload={{
                        action: INPUT_CHANGED,
                        payload: { term: '' },
                    }}
                    bare
                    type="button"
                    size={buttonSize}
                    icon={cancelIconName}
                    className="input-container__button-icon input-container__icon--cancel"
                    on-focus={() => {
                        if (hasSuggestions) {
                            dispatch(SHOW_POPOVER);
                        }
                    }}
                    variant="tertiary"
                    config-aria={{ 'aria-label': LABELS.CLEAR_SEARCH }}
                    tooltip-content={LABELS.CLEAR_SEARCH}
                />
            ) : null}
            {isMobileView ? null : (
                <now-button-iconic
                    append-to-payload={{ action: ITEM_SELECTED }}
                    bare
                    type="button"
                    size={buttonSize}
                    icon={searchIconName}
                    on-focus={() => {
                        if (hasSuggestions) {
                            dispatch(SHOW_POPOVER);
                        }
                    }}
                    on-blur={() => {
                        dispatch(HIDE_POPOVER);
                    }}
                    className="input-container__button-icon input-container__icon--search"
                    variant="tertiary"
                    config-aria={{ 'aria-label': LABELS.SUBMIT_SEARCH }}
                    tooltip-content={LABELS.SUBMIT_SEARCH}
                />
            )}
        </div>
    );
};

export default view;
