import '@servicenow/now-button';
import isEmpty from 'lodash/isEmpty';
import MobileInput from './MobileInput';
import Popover from '../../common/view/Popover';
import MobileStartViewInHeader from './MobileStartViewInHeader';
import MobileStartViewInPage from './MobileStartViewInPage';

const view = (state, { dispatch }) => {
    const {
        searchPanelOpened,
        showPopover,
        term,
        activeRowIndex,
        data,
        inputHasFocus,
        properties: { placeholder, placement, nowId },
    } = state;

    const hasSuggestions = data && data.length > 0;
    const ariaPopupType = isEmpty(term) ? 'grid' : 'listbox';

    if (searchPanelOpened) {
        return (
            <div role="search">
                <div
                    className="search-combobox search-combobox--mobile"
                    role="combobox"
                    aria-controls={
                        showPopover ? `${nowId}_owned_grid` : undefined
                    }
                    aria-haspopup={ariaPopupType}
                    aria-autocomplete="list"
                    aria-expanded={showPopover ? 'true' : 'false'}
                >
                    <MobileInput
                        hasFocus={inputHasFocus}
                        showPopover={showPopover}
                        placeholder={placeholder}
                        term={term}
                        dispatch={dispatch}
                        hasSuggestions={hasSuggestions}
                        nowId={nowId}
                    />
                    {showPopover ? (
                        <Popover
                            activeRowIndex={activeRowIndex}
                            data={data}
                            term={term}
                            dispatch={dispatch}
                            nowId={nowId}
                        />
                    ) : null}
                </div>
            </div>
        );
    }
    return (
        <div role="search">
            {placement === 'header' ? (
                <MobileStartViewInHeader dispatch={dispatch} />
            ) : (
                <MobileStartViewInPage
                    placeholder={placeholder}
                    dispatch={dispatch}
                />
            )}
        </div>
    );
};

export default view;
