import '@servicenow/now-icon';
import '@servicenow/now-button';
import { MOUSE_OVERED, ITEM_SELECTED } from '../../actions';
import { LABELS } from '../../constants';

const createActions = (actions, term) =>
    (actions || []).map(action => {
        const { clickActionType, icon } = action;
        return (
            <now-button-iconic
                append-to-payload={{
                    action: clickActionType,
                }}
                className="search-popover__item-button"
                size="md"
                variant="tertiary"
                bare
                icon={icon}
                tabIndex={-1}
                config-aria={{
                    'aria-label': `${LABELS.CLEAR_RECENT_SEARCH}: ${term}`,
                }}
                tooltip-content={`${LABELS.CLEAR_RECENT_SEARCH}: ${term}`}
                role="gridcell"
            />
        );
    });

const view = ({
    icon,
    isActive,
    index,
    dispatch,
    primaryText,
    suggestionLabel = '',
    showActions,
    itemActions = [],
}) => {
    const actions = createActions(itemActions, suggestionLabel);
    const onItemSelection = evt => {
        // button-bare dispatches its own action
        if (!evt.target.matches('now-button-iconic')) {
            dispatch(ITEM_SELECTED);
        }
    };

    return (
        <li
            className={`search-popover__item ${
                isActive ? 'search-popover__item--is-active' : ''
            }`}
            on-mousedown={onItemSelection}
            on-mouseover={() => {
                dispatch(MOUSE_OVERED, { index });
            }}
            role="row"
            aria-selected={isActive}
            tabIndex={-1}
        >
            <div
                className="search-popover__item-metadata"
                tabIndex={-1}
                role="gridcell"
            >
                <now-icon
                    icon={icon}
                    size="md"
                    className="search-popover__item-icon"
                />
                <div className="search-popover__item-primary-text">
                    <p>{primaryText}</p>
                </div>
            </div>
            {showActions ? actions : null}
        </li>
    );
};

export default view;
