const createQuerySelector = querySelector => host =>
    host.shadowRoot.querySelector(querySelector);

const getPopoverEl = createQuerySelector('ul.search-popover');
const getInputContainerEl = createQuerySelector('div.input-container');
const isMobileCombobox = host => host.tagName.includes('MOBILE');

const fitResizeEffect = ({ host }) => {
    const contentElem = getPopoverEl(host);
    const targetElem = getInputContainerEl(host);
    const isMobileComponent = isMobileCombobox(host);

    if (isMobileComponent) return;

    // NOT the best way but issues with removing inline style and then updating it via normal
    // cycle
    if (contentElem) {
        const currentWidth = contentElem.style.width;
        const targetWidth = `${targetElem.offsetWidth}px`;
        if (currentWidth !== targetWidth)
            Object.assign(contentElem.style, { width: targetWidth });
    }
};

export default fitResizeEffect;
