import {
    SHOW_SEARCH_PANEL,
    HIDE_SEARCH_PANEL,
    MOBILE_CANCEL_BUTTON_CLICKED,
} from '../actions';

import {
    showSearchPanelInMobileViewEffect,
    hideSearchPanelInMobileViewEffect,
    handleMobileCancel,
} from '../effects';

import baseActionHandlers from '../../common/actionHandlers';

const mobileActionHandlers = {
    ...baseActionHandlers,
    [SHOW_SEARCH_PANEL]: {
        effect: showSearchPanelInMobileViewEffect,
        stopPropagation: true,
    },
    [HIDE_SEARCH_PANEL]: {
        effect: hideSearchPanelInMobileViewEffect,
        stopPropagation: true,
    },
    [MOBILE_CANCEL_BUTTON_CLICKED]: {
        effect: handleMobileCancel,
        stopPropagation: true,
    },
};

export default mobileActionHandlers;
