import isEmpty from 'lodash/isEmpty';
import {
    POPOVER_ITEM_TYPE,
    KEYBOARD_NAVIGATION,
    RECENT_SEARCH_SECTION_HEADER,
} from '../constants';

const _recentSearchSection = data => {
    if (isEmpty(data)) return false;
    return data
        .filter(option => option.type === POPOVER_ITEM_TYPE.SECTION_HEADER)
        .some(
            option =>
                option.columns[0].displayValue.toLowerCase() ===
                RECENT_SEARCH_SECTION_HEADER.toLowerCase()
        );
};

const getRowColIndex = (activeRowIndex, activeColumnIndex, data, direction) => {
    const len = data.length;
    if (len === 0) {
        return { rowIndex: -1, columnIndex: 0 };
    }
    let columnIndex = activeColumnIndex;
    let rowIndex = activeRowIndex;
    const recentSearchSection = _recentSearchSection(data);

    switch (direction) {
        case KEYBOARD_NAVIGATION.UP:
            do {
                rowIndex = rowIndex < 1 ? -1 : rowIndex - 1;
                // move focus to input
                if (rowIndex === -1) {
                    columnIndex = 0;
                    break;
                }
                if (data[rowIndex].type !== POPOVER_ITEM_TYPE.USER_HISTORY)
                    columnIndex = 1;
                else columnIndex = columnIndex > 0 ? columnIndex : 1;
            } while (data[rowIndex].type === POPOVER_ITEM_TYPE.SECTION_HEADER);
            break;
        case KEYBOARD_NAVIGATION.DOWN:
            do {
                rowIndex = rowIndex === len - 1 ? rowIndex : rowIndex + 1;
                // when focus is on the last element don't move the focus
                if (rowIndex === len - 1) {
                    columnIndex = 1;
                    break;
                }
                if (data[rowIndex].type !== POPOVER_ITEM_TYPE.USER_HISTORY)
                    columnIndex = 1;
                else columnIndex = columnIndex > 0 ? columnIndex : 1;
            } while (data[rowIndex].type === POPOVER_ITEM_TYPE.SECTION_HEADER);
            break;
        case KEYBOARD_NAVIGATION.LEFT:
            do {
                // no grid navigation
                if (
                    data[rowIndex].type !== POPOVER_ITEM_TYPE.USER_HISTORY ||
                    !recentSearchSection
                ) {
                    rowIndex = rowIndex < 1 ? len - 1 : rowIndex - 1;
                    columnIndex = 1;
                } // grid navigation
                else if (columnIndex === 1) {
                    rowIndex = rowIndex < 1 ? len - 1 : rowIndex - 1;
                    columnIndex += 1;
                } else {
                    columnIndex -= 1;
                }
            } while (data[rowIndex].type === POPOVER_ITEM_TYPE.SECTION_HEADER);
            break;
        case KEYBOARD_NAVIGATION.RIGHT:
            do {
                // no grid navigation
                if (
                    data[rowIndex].type !== POPOVER_ITEM_TYPE.USER_HISTORY ||
                    !recentSearchSection
                ) {
                    rowIndex = (rowIndex + 1) % len;
                    columnIndex = 1;
                } // grid navigation
                else if (columnIndex > 1) {
                    rowIndex = (rowIndex + 1) % len;
                    columnIndex -= 1;
                } else {
                    columnIndex += 1;
                }
            } while (data[rowIndex].type === POPOVER_ITEM_TYPE.SECTION_HEADER);
            break;
        default:
            break;
    }

    return { rowIndex, columnIndex };
};

export default getRowColIndex;
