function createAfterInterceptor(actionNames) {
    let actions = actionNames;
    if (!Array.isArray(actionNames)) {
        actions = [actionNames];
    }
    return actions.map(actionName => ({
        after: context => ({
            ...context,
            effects: [
                ...context.effects,
                {
                    effect: ({ dispatch }) => dispatch(actionName),
                    args: [context.coeffects],
                },
            ],
        }),
    }));
}

export default createAfterInterceptor;
