import Item from './PopoverItem';
import highlightMatch from '../../utils/highlight';

const view = ({ term, isActive, name, icon, index, dispatch }) => {
    const actions = [];
    const primaryText = highlightMatch(term, name);

    return (
        <Item
            isActive={isActive}
            index={index}
            icon={icon}
            primaryText={primaryText}
            itemActions={actions}
            dispatch={dispatch}
        />
    );
};

export default view;
