import './sn-search-combobox-desktop';
import './sn-search-combobox-mobile';
import { SIZE } from './common/constants';

const view = ({ properties }) => {
    const {
        placeholder,
        searchTerm,
        searchContextConfigId,
        placement,
        disableAutocomplete,
        size,
    } = properties;

    const searchComboboxVariant =
        size === SIZE.SMALL ? (
            <sn-search-combobox-mobile
                placeholder={placeholder}
                searchTerm={searchTerm}
                searchContextConfigId={searchContextConfigId}
                placement={placement}
                disableAutocomplete={disableAutocomplete}
            />
        ) : (
            <sn-search-combobox-desktop
                placeholder={placeholder}
                searchTerm={searchTerm}
                searchContextConfigId={searchContextConfigId}
                placement={placement}
                disableAutocomplete={disableAutocomplete}
            />
        );
    return searchComboboxVariant;
};

export default view;
