import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import keyBindingBehavior from '@servicenow/behavior-key-binding';
import rtlBehavior from '@servicenow/behavior-rtl';
import { t } from 'sn-translate';
import style from './index.scss';
import view from './view/Combobox';
import actionHandlers from './actionHandlers';
import {
    ARROW_DOWN_PRESSED,
    ARROW_UP_PRESSED,
    ARROW_LEFT_PRESSED,
    ARROW_RIGHT_PRESSED,
    HIDE_POPOVER,
} from './actions';
import {
    enterKeyHandler,
    tabKeyHandler,
    _isPopoverButtonEvent,
    _isPopoverTermEvent,
} from '../common/effects/inputEffects';

createCustomElement('sn-search-combobox-desktop', {
    renderer: { type: snabbdom },
    styles: style,
    view,
    actionHandlers,
    eventHandlers: [
        {
            events: ['click'],
            effect({
                state,
                dispatch,
                action: {
                    payload: { event, host },
                },
            }) {
                const { showPopover } = state;
                if (showPopover) {
                    const path = event.composedPath
                        ? event.composedPath()
                        : event.path;
                    const hostInPath = path.includes(host);

                    if (!hostInPath) dispatch(HIDE_POPOVER);
                }
            },
            target: document,
        },
    ],
    behaviors: [
        {
            behavior: keyBindingBehavior,
            options: {
                keyBindings: {
                    pressArrowUp: ['ArrowUp'],
                    pressArrowDown: ['ArrowDown'],
                    pressArrowLeft: ['ArrowLeft'],
                    pressArrowRight: ['ArrowRight'],
                    pressEscape: ['Escape'],
                    pressEnter: ['Enter'],
                    pressTab: ['Tab'],
                },
                handlers: {
                    pressArrowUp: (host, event, dispatch) => {
                        event.preventDefault(); // prevent scrolling up
                        dispatch(ARROW_UP_PRESSED, { host });
                    },
                    pressArrowDown: (host, event, dispatch) => {
                        event.preventDefault(); // prevent scrolling down
                        dispatch(ARROW_DOWN_PRESSED, { host });
                    },
                    pressArrowLeft: (host, event, dispatch) => {
                        if (
                            _isPopoverButtonEvent(event) ||
                            _isPopoverTermEvent(event)
                        )
                            dispatch(ARROW_LEFT_PRESSED, { host });
                    },
                    pressArrowRight: (host, event, dispatch) => {
                        if (
                            _isPopoverButtonEvent(event) ||
                            _isPopoverTermEvent(event)
                        )
                            dispatch(ARROW_RIGHT_PRESSED, { host });
                    },
                    pressEscape: (host, event, dispatch) => {
                        dispatch(HIDE_POPOVER);
                        const input = host.shadowRoot.querySelector('input');
                        if (input) input.focus();
                    },
                    pressEnter: enterKeyHandler,
                    pressTab: tabKeyHandler,
                },
            },
        },
        { behavior: rtlBehavior },
    ],
    initialState: {
        term: '',
        suggestionSearchTermQuery: null,
        activeRowIndex: -1,
        activeColumnIndex: 0,
        showPopover: false,
        data: [],
        width: 0,
        inputHasFocus: false,
        targetRef: null,
    },
    properties: {
        placeholder: { default: t('Search') },
        searchTerm: { default: '' },
        searchContextConfigId: { default: '' },
        placement: { default: 'body' },
        disableAutocomplete: { default: false },
    },
});
